<script>
  import { onMount } from 'svelte';
  import { format } from "d3-format";
  import { timeParse, timeFormat } from "d3-time-format";
  import { LayerCake, Svg, Html } from 'layercake';
  import { scaleBand } from 'd3-scale';
  import totalDeath from "./data/Total deaths attributed to Covid-19 per million population (per capital).csv";
  import allData from "./data/Datasheet for NS data project - All.csv";
  import excessDeaths from "./data/Excess deaths.csv"
  import lockdownMeasure from './data/Key lockdown measures.csv';
  import testingMeasure from './data/A testing time.csv';
  import stimulusPackage from './data/Stimulus package.csv';
  import sevenDayAverageDeathsCsv from './data/Seven Day Average Deaths.csv';
  import stringencyCsv from './data/STRINGENCY LINE CHART DATA.csv';
  import chartMeta from './data/chartMeta.csv';
  import creditData from './data/credit-rating.csv';
  import debtContractRelief from './data/Speed and depth of debt or contract relief.csv'
  import GroupedBar from "./graphics/grouped_bar.svelte";
  import PoliticalLine2 from "./graphics/political_line_2.svelte";
  import ColumnChart from "./graphics/column_chart.svelte";
  import TextureColumnChart from "./graphics/texture_column_chart.svelte";
  import SmallMultipleMultiline from './graphics/small_multiple_multiline.svelte';
  import SmallMultipleLine from './graphics/small_multiple.svelte';
  import DumbbellChart from './graphics/dumbbell_chart.svelte';
  import PeakLine from './graphics/peak_line.svelte';
  import PoliticalLine from './graphics/political_line.svelte';
  import ScatterPlot from './graphics/scatter_plot.svelte';
  export let pymChild = {};
  export let styleNs = false; // serif or san-serif;
  export let title = "NSMG-018";
  function updateHeight() {
    setTimeout(() => {
      pymChild.sendHeight()
    }, 50);
  }
  let seriesColors = ["#E60032","#0063E6","#E6C800","#E67800","#00E682"];
  const colorScale = {
    "BRA": seriesColors[2],
    "CAN": "#8399A8",
    "CHN": seriesColors[4],
    "DEU": "#8399A8",
    "ESP": "#8399A8",
    "FRA": "#8399A8",
    "GBR": seriesColors[0],
    "IND": "#8399A8",
    "ITA": seriesColors[3],
    "JPN": "#8399A8",
    "KOR": "#8399A8",
    "RUS": "#8399A8",
    "SWE": "#8399A8",
    "USA": seriesColors[1]};
  
  const countries = ["BRA","CAN","CHN","DEU","ESP","FRA","GBR","IND","ITA","JPN","KOR","RUS","SWE","USA"];
  const countryNames = ["Brazil","Canada","China","Germany","Spain","France","UK","India","Italy","Japan","Korea","Russia","Sweden","USA"];
  const stringency = stringencyCsv.map(d => ({
    ...d,
    deaths: +d.deaths,
    value: +d.value,
    title: countryNames[countries.indexOf(d.country)],
  }));
  const sevenDayAverageDeaths = sevenDayAverageDeathsCsv.map(d => ({
    ...d,
    title: countryNames[countries.indexOf(d.country)]
  }))
  let sectionData = [
    {
      title: "Deaths and disease",
      isOpen: true,
      summaryTitle: "Total deaths attributed to Covid-19",
      fieldId: 42,
      chartMeta: chartMeta.slice(0,5)
    },
    {
      title: "Lockdown",
      isOpen: true,
      summaryTitle: "Efficacy of lockdown - drop in personal mobility",
      reverse: true,
      fieldId: 61,
      chartMeta: chartMeta.slice(5,8)
    },
    {
      title: "Economic",
      isOpen: true,
      summaryTitle: "Forecast GDP change in 2020",
      fieldId: 105,
      isGrid: true,
      chartMeta: [chartMeta[8], chartMeta[9]],
      chartData: [
        {...chartMeta[10], fieldId: 105},
        {...chartMeta[11], fieldId: 107},
        {...chartMeta[12], fieldId: 107},
        {...chartMeta[13], fieldId: 17}
      ]
    },
    {
      title: "Political",
      isOpen: true,
      summaryTitle: "Approval ratings change",
      chartMeta: [chartMeta[14], chartMeta[15], chartMeta[15]],
      fieldId: 89
    },
    {
      title: "Preparedness",
      isOpen: true,
      isGrid: true,
      chartData: [
        {...chartMeta[16], fieldId: 70 },
        {...chartMeta[17], fieldId: 72 },
        {...chartMeta[18], fieldId: 74 },
        {...chartMeta[19], fieldId: 78 },
        {...chartMeta[20], fieldId: 82 },
        {...chartMeta[21], fieldId: 80 },
        {...chartMeta[22], fieldId: 81 },
       ]
    },
  ];
  let lockdownAnnotations = [
    {fieldId: 53, title: "restrictions being placed on gatherings of 10 people or less."},
    {fieldId: 54, title: "restrictions being placed on internal movement."},
    {fieldId: 55, title: "banning arrivals from some regions."},
    {fieldId: 56, title: "all public events being cancelled."},
    {fieldId: 57, title: "schools being required to close at all levels."},
    {fieldId: 58, title: "people being made to stay home besides exceptions for daily exercise, grocery shopping, and 'essential' trips."},
    {fieldId: 59, title: "public transport being closed or prohibited for most citizens."},
    {fieldId: 60, title: "some sectors being closed or made to work from home."},
  ];
  let peakAnnotation = [
    {fieldId: 49, title: "time to 20% from peak"}
  ]
  let pollingData = [
    {fieldId: 85, title: "First death"},
    {fieldId: 86, title: "500th death"},
    {fieldId: 87, title: "10,000th death"},
    {fieldId: 88, title: "June 25"},
  ]
  lockdownAnnotations = lockdownAnnotations.map(d => {
    return countries.map(e => ({
      country: e,
      title: d.title,
      value: allData.filter(f => +f.id === d.fieldId)[0][e]
  }))});
  peakAnnotation = peakAnnotation.map(d => {
    return countries.map(e => ({
      country: e,
      title: d.title,
      value: allData.filter(f => +f.id === d.fieldId)[0][e]
  }))});
  pollingData = countries.map(e => {
    return {
      key: e,
      sort: colorScale[e] === "#8399A8" ? 0 : 1,
      values: pollingData.map(d => ({
      key: d.title,
      value: +allData.filter(f => +f.id === d.fieldId)[0][e],
      fill: colorScale[e]
  })).filter(d => d.value !== 0 && !isNaN(d.value))}
  }).sort((a, b) => a.sort - b.sort);
  sectionData = sectionData.map(d => ({
    ...d,
    data: !d.fieldId ? undefined : countries.map(e => ({
      country: e,
      y: (d.reverse ? -1 : 1) * allData.filter(f => +f.id === d.fieldId)[0][e].replace(/,/g, "")
    })),
    chartData: d.chartData ? d.chartData.map(g => ({
      ...g,
      data: countries.map(e => ({
        country: e,
        y: (d.reverse ? -1 : 1) * allData.filter(f => +f.id === g.fieldId)[0][e].replace(/,/g, "")
      }))
    })) : undefined,
  }));
  let creditRating = creditData.map(e => ({
    code: e.country,
    country: countryNames[countries.indexOf(e.country)],
    jan: e.rating_jan,
    may: e.latest_rating,
    outlook: e.outlook,
    diff: e.change_numeric
  }))
  onMount(() => {
    pymChild.sendHeight();
    sectionData[0].isOpen = false;
    sectionData[1].isOpen = false;
    sectionData[2].isOpen = false;
    sectionData[3].isOpen = false;
    sectionData[4].isOpen = false;
  });
</script>

